saneamiento e higiene del edificio, las dependencias, el equipamiento y los utensilios. Esto incluye controlar la calidad y potabilidad del agua y del aire, asegurando su adecuada ventilación y circulación. Se deben establecer procedimientos eficientes para la gestión de desperdicios y desechos, incluyendo su recolección, almacenamiento y eliminación, para evitar la proliferación de plagas y la contaminación ambiental. Se deben implementar medidas para controlar la presencia de plagas, como insectos y roedores, asegurando la limpieza y desinfección de las instalaciones para evitar la contaminación. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-3",attrs:{"x":"68%","y":"46%","numero":"C"}},[_c('div',{staticClass:"h5 mb-2 texto-color-acento-botones"},[_vm._v("Edificio y terreno")]),_c('p',{staticClass:"text-dark text-small"},[_vm._v("El diseño y la construcción del edificio deben estar diseñados para proteger los alimentos de la contaminación y evitar el desarrollo de microorganismos. Las áreas de trabajo deben ser aisladas para prevenir la entrada de polvo, suciedad y plagas. Los materiales utilizados en la construcción del edificio deben ser resistentes a la humedad, las grasas y los químicos. El edificio debe contar con un sistema de ventilación adecuado para evitar la acumulación de humedad y la proliferación de microorganismos. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-3",attrs:{"x":"60%","y":"72%","numero":"D"}},[_c('div',{staticClass:"h5 mb-2 texto-color-acento-botones"},[_vm._v("Equipamiento y utensilios")]),_c('p',{staticClass:"text-dark text-small"},[_vm._v("El diseño de los equipos y utensilios empleados en la cocina debe facilitar su limpieza y mantenimiento, previniendo la contaminación de los alimentos. Los materiales de los equipos y utensilios deben ser no porosos, resistentes a la corrosión, al calor y fáciles de limpiar. Se deben establecer reglas para el manejo higiénico de los equipos y utensilios, incluyendo su limpieza y desinfección regular. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-3",attrs:{"x":"40%","y":"72%","numero":"E"}},[_c('div',{staticClass:"h5 mb-2 texto-color-acento-botones"},[_vm._v("Procesos y controles")]),_c('p',{staticClass:"text-dark text-small"},[_vm._v("Durante la manipulación de la materia prima y la preparación de alimentos, se deben establecer controles para minimizar el riesgo de contaminación: se deben implementar prácticas para evitar la proliferación de microorganismos en los alimentos, incluyendo la cocción adecuada, el almacenamiento en frío y el uso de técnicas de conservación. Se debe utilizar productos de limpieza y desinfección seguros y evitar el uso de productos químicos que puedan contaminar los alimentos. Se debe mantener el área de trabajo limpia y ordenada, evitando la acumulación de suciedad y residuos. Se deben establecer procedimientos para el manejo seguro de los alimentos contaminados, incluyendo su identificación, separación y eliminación. ")])]),_c('div',{staticClass:"tarjeta color-acento-botones p-3",attrs:{"x":"31%","y":"46%","numero":"F"}},[_c('div',{staticClass:"h5 mb-2 texto-color-acento-botones"},[_vm._v("Registros")]),_c('p',{staticClass:"text-dark text-small"},[_vm._v("Los registros son un elemento fundamental para el control de las buenas prácticas. Se deben llevar registros de: limpieza y desinfección de las instalaciones, equipos y personal, inspección de la materia prima, productos y preparaciones, temperatura y condiciones de transporte de los alimentos y cualquier otra información relevante para la gestión de la seguridad alimentaria.")])])])],1)]),_vm._m(6),_c('div',{staticClass:"bg_img_6 fondo_2_1_3"},[_vm._m(7),_c('div',{staticClass:"row align-items-center justify-content-center mb-3"},[_c('div',{staticClass:"col-lg-12 mb-5"},[_c('div',{staticClass:"bloque-texto-g bloque-texto-g color-secundario p-3 p-sm-4 p-md-5"},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url("