uenta los siguientes elementos: ")]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8 mb-3"},[_c('LineaTiempoD',[_c('p',{attrs:{"numero":"1","titulo":"Diseño higiénico"}},[_vm._v("Las cocinas deben construirse para evitar la acumulación de suciedad y permitir operaciones de limpieza eficaces. Las superficies deben ser continuas y de fácil acceso para la limpieza y desinfección, evitando ángulos y grietas donde se pueda acumular suciedad.")]),_c('p',{attrs:{"numero":"2","titulo":"Distribución interna"}},[_vm._v("Una adecuada distribución de los espacios en la cocina es esencial para prevenir la contaminación cruzada y optimizar el flujo de trabajo. Esto incluye la correcta disposición de equipos y áreas de trabajo, asegurando que el movimiento de materiales y personas no comprometa la higiene (Ministerio de Salud y Protección Social, 2013, Art. 6).")]),_c('p',{attrs:{"numero":"3","titulo":"Control de plagas"}},[_vm._v("Es obligatorio construir las instalaciones de forma que se evite la entrada de plagas, como insectos y roedores. Todas las posibles entradas deben ser selladas adecuadamente para asegurar que estas no pueden acceder a las áreas de preparación de alimentos.")]),_c('p',{attrs:{"numero":"4","titulo":"Materialidad de superficies"}},[_vm._v("Las superficies deben ser antideslizantes para garantizar la seguridad de los trabajadores, pero también deben ser lisas para facilitar la limpieza. Los materiales como las resinas poliméricas son ideales por su durabilidad y facilidad de mantenimiento (Ministerio de Salud y Protección Social, 2013, Art. 8).")]),_c('p',{attrs:{"numero":"5","titulo":"Revestimiento de paredes"}},[_vm._v("El revestimiento ideal para paredes en cocinas es la cerámica o el acero inoxidable, debido a su resistencia a la humedad y facilidad de limpieza. Estos materiales deben aplicarse en toda la superficie de trabajo, cubriendo desde el piso hasta el techo para evitar zonas de acumulación de grasa o residuos. Para su mantenimiento, se recomienda limpiarlos diariamente con desinfectantes aprobados para uso alimentario y realizar revisiones regulares para reparar posibles fisuras o desgastes que puedan comprometer la higiene.")]),_c('p',{attrs:{"numero":"6","titulo":"Prevención de condensación"}},[_vm._v("Los techos deben diseñarse para evitar la acumulación de humedad que pueda provocar condensación y goteo sobre áreas donde se manipulan alimentos. El control de la temperatura y la humedad es esencial para prevenir estos riesgos (Ministerio de Salud y Protección Social, 2013, Art. 9).")])])],1),_vm._m(16)]),_vm._m(17),_c('p',{staticClass:"mb-3"},[_vm._v("Los utensilios, recipientes, envases y envolturas utilizados en la cocina deben cumplir con estrictos requisitos de higiene para asegurar la inocuidad de los alimentos. Según la Resolución 2674 de 2013 del Ministerio de Salud y Protección Social de Colombia, es imperativo que estos elementos se encuentren en perfecto estado de conservación e higiene para prevenir la contaminación cruzada (Ministerio de Salud y Protección Social, 2013, Art. 5).")]),_vm._m(18),_vm._m(19),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-12 mb-3"},[_c('div',{staticClass:"bloque-texto-g bloque-texto-g color-secundario p-3 p-sm-4 p-md-5"},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/temas/tema2/img_16.png')) + ")")})}),_vm._m(20)])])]),_vm._m(21),_vm._m(22),_c('h4',{staticClass:"text-center mb-3"},[_vm._v("Evaluación de la materia prima: al adquirir una materia prima, se deben evaluar los siguientes factores:")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-8 mb-3"},[_c('LineaTiempoD',[_c('p',{attrs:{"numero":"1","titulo":"Calidad organoléptica"}},[_vm._v("Los manipuladores deben tener conocimiento de la calidad organoléptica de los alimentos, es decir, su 