l transporte. Sin embargo, existen esfuerzos para mejorar la situación mediante la implementación de nuevos vehículos con sistemas de refrigeración adecuados. ")]),_vm._m(26),_vm._m(27),_vm._m(28),_vm._m(29),_vm._m(30),_vm._m(31),_vm._m(32),_vm._m(33),_vm._m(34),_vm._m(35),_c('div',{staticClass:"row justify-content-center align-items-center mb-3"},[_c('div',{staticClass:"col-lg-8 mb-4 order-2 order-lg-1"},[_c('LineaTiempoD',[_c('p',{attrs:{"numero":"a","titulo":"Prácticas culinarias"}},[_vm._v("La limpieza y organización son claves para prevenir la contaminación cruzada. Mantener las mesadas, tablas de trabajo y utensilios limpios y separados para alimentos crudos y cocidos es fundamental. El lavado de manos frecuente, especialmente antes de manipular alimentos, así como el uso de guantes en situaciones que lo requieran, también son medidas cruciales. Evitar la acumulación de residuos, alimentos o utensilios sucios en el área de trabajo y respetar la cadena de frío son aspectos a tener en cuenta para mantener un entorno higiénico.")]),_c('p',{attrs:{"numero":"b","titulo":"Cocción"}},[_vm._v("La cocción es un proceso fundamental para eliminar los microorganismos presentes en los alimentos. Es esencial que la temperatura alcance los 75°C en el centro del alimento durante el tiempo adecuado para asegurar su inocuidad. Al cocinar, se debe tener cuidado con las temperaturas de cocción, el tiempo de cocción y la distribución uniforme del calor para garantizar que todos los puntos del alimento alcancen la temperatura segura. ")]),_c('p',{attrs:{"numero":"c","titulo":"Enfriamiento"}},[_vm._v("El enfriamiento rápido de los alimentos después de la cocción es crucial para evitar la proliferación bacteriana. Mantener los alimentos refrigerados a una temperatura de 4°C o menos previene el crecimiento de microorganismos. Se debe evitar el enfriamiento lento y la exposición a temperaturas ambiente, especialmente en zonas de riesgo.")]),_c('p',{attrs:{"numero":"d","titulo":"Congela